ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: black;
  margin-right: 0px;
}

li {
  float: right;
}

li a {
  display: inline-flex;
  color: rgb(138, 111, 111);
  text-align: center;
  padding: 10px 10px;
  text-decoration: none;
  text-transform: uppercase;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  letter-spacing: 1px;
  word-spacing: 3px;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
  background-color: rgb(17, 17, 17);
  color: snow;
  transition: 0.5s;
}
.web {
  float: left;
  margin-left: 0px;
}

@media only screen and (max-width: 420px) {
  li a {
    display: inline-flex;
    color: rgb(138, 111, 111);
    text-align: center;
    padding: 15px 5px;
    text-decoration: none;
    text-transform: uppercase;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    letter-spacing: 1px;
    word-spacing: 3px;
  }
  li {
    float: right;
    margin-right: 0px;
  }
  .web {
    float: left;
    margin-left: 0px;
  }
}
